import { createApp } from './app';
import Vue from 'vue';
import {
  Pagination,
  Input,
  Form,
  FormItem,
  Button,
  Message,
  Icon,
  Loading
} from 'element-ui';

Vue.use(Pagination);
Vue.use(Input);
Vue.use(Form);
Vue.use(FormItem);
Vue.use(Button);
Vue.use(Icon);
Vue.use(Loading);
Vue.prototype.$loading = Loading.service;
Vue.prototype.$message = Message;

const { app, router, store } = createApp();
if (window.__INITIAL_STATE__) {
  store.replaceState(window.__INITIAL_STATE__);
}
router.onReady(() => {
  console.log(1)
  //  asyncData.
  //  resolve 
  // (double-fetch)
  //  `router.beforeResolve()` resolve
  router.beforeResolve((to, from, next) => {
    console.log(2)
    const matched = router.getMatchedComponents(to);
    const prevMatched = router.getMatchedComponents(from);
    // 
    // 
    let diffed = false;
    const activated = matched.filter((current, index) => {
      return diffed || (diffed = prevMatched[index] !== current);
    });
    if (!activated.length) {
      return next();
    }

    //  (loading indicator)

    Promise.all(
      activated.map(item => {
        if (item.asyncDat) {
          return item.asyncDat({ store, route: to });
        }
      })
    )
      .then(() => {
        // (loading indicator)
        next();
      })
      .catch(next);
  });
  app.$mount('#app');
});
